.support {
  width: 100%;
}

.social {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 40px 0;
}

.social .item i {
  font-size: 60px;
  color: #ccc;
  cursor: pointer;
  -webkit-transition: .1s all;
  -o-transition: .1s all;
  transition: .1s all;
}

.social .item i:hover {
  color: #000;
  -webkit-transition: .1s all;
  -o-transition: .1s all;
  transition: .1s all;
}


